<template>
  <div id="inviteList" ref="inviteList">
    <div class="invite-list" v-if="list && list.length > 0">
      <div class="" v-for="(item,index) in list" @click="setItem(index)" :key="index">
        <!-- 正常状态 -->
        <div class="inviteItem flex-row jusB" v-if="item.status === 1">
          <div class="leftContent flex-row">
            <div class="leftIcon iconSize">
              <img :src="item.invitee_avatar" alt="头像">
            </div>
            <div class="nickname">{{$utils.splitStr(item.invitee_nickname, 6)}}</div>
          </div>
          <div class="rightContent flex-column level-flex-end">
            <div class="top flex-row hoz-flex-end time">{{item.create_time ? $utils.timestampToStr(item.create_time * 1000, 'h:m:s') :'无'}}</div>
            <div class="top flex-row hoz-flex-end date">{{item.create_time ? $utils.timestampToStr(item.create_time * 1000, 'Y-M-D') :'无'}}</div>
          </div>
        </div>
        <!-- 失效状态 -->
        <div class="inviteItem flex-row failure jusB" v-else>
          <div class="leftContent flex-row">
            <div class="leftIcon iconSize">
              <img :src="item.invitee_avatar" alt="头像">
            </div>
            <div class="nickname">{{$utils.splitStr(item.invitee_nickname, 6)}}</div>
            <div class="statusButton flex-row">
              <span>失效</span>
            </div>
          </div>
          <div class="rightContent flex-column jusC">
            <div class="top flex-row hoz-flex-end date">{{item.create_time ? $utils.timestampToStr(item.create_time * 1000, 'Y-M-D') :'无'}}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="noList" v-else>
      <div class="flex-row">
        <div class="iconSize">
          <img src="../../../assets/img/task/no_invite_list.png" alt="无邀请列表">
        </div>
      </div>
      <div class="flex-row">
        <div class="f24 color999">你还没有邀请过好友</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    list: {
      type: Array,
      default: null
    }
  },
  methods: {
    setItem (index) {
      let item = this.list[index]
      if (item.status === 0) {
        this.$toast({
          message: '该账户数据被清除，已停用',
          duration: 1500
        })
      }
    }
  },
  mounted () {
  }
  
}
</script>
<style lang="scss" scoped>
#inviteList{
  width: 100%;
  overflow: hidden;
  padding-bottom: 20px;
  .noList{
    .flex-row{
      &:nth-child(1) {
        padding: 156px 0 30px;
      }
      .iconSize{
        width: 169px;
        height: 107px;
      }
    }
    padding-bottom: 160px;
  }
  .inviteItem {
    padding: 20px 30px;
    font-size: 30px;
    color: #232323;
    border-bottom: 1px solid #d8d8d8;
    .leftIcon{
      width: 70px;
      height: 70px;
      margin-right: 20px;
      border-radius: 50%;
      overflow: hidden;
    }
    .rightContent{
      font-size: 24px;
      color: #999;
      height: 60px;
      display: flex;
      justify-content: space-between;
    }
    .statusButton {
      margin-left: 14px;
      background: #999;
      font-size: 26px;
      color: #fff;
      border-radius: 44px;
      box-sizing: border-box;
      width: 104px;
      height: 44px;
      span {
        padding-top: 4px;
      }
    }
    &.failure {
      background: #eee;
      .rightContent {
        justify-content: center;
      }
    }
  }
}
</style>



