<template>
  <div id="whatCode">
    <div class="whatCodeBox posC">
      <div class="w_top flex-row">什么是邀请码</div>
      <div class="w_tip">
        <div class="w_t_text">邀请码是你的app身份标识</div>
        <div class="w_t_text">好友进入掘金宝后，在“任务”页面填写你的邀请码，就算邀请成功！</div>
        <div class="w_t_text third redColor">将邀请码粘贴到微信、qq、微博、头条等，都能快速涨粉！</div>
      </div>
      <div class="shareBox flex-row">
        <div class="shereIcon">
          <div class="icon">
            <div class="iconSize iconSize1">
              <img src="../../../assets/img/task/task_qq.png" alt="qq">
            </div>
          </div>
          <div class="text">微信</div>
        </div>
        <div class="shereIcon">
          <div class="icon">
            <div class="iconSize iconSize2">
              <img src="../../../assets/img/task/task_friend.png" alt="朋友圈">
            </div>
          </div>
          <div class="text">朋友圈</div>
        </div>
        <div class="shereIcon">
          <div class="icon">
            <div class="iconSize iconSize3">
              <img src="../../../assets/img/task/task_wechat.png" alt="微信">
            </div>
          </div>
          <div class="text">qq</div>
        </div>
        <div class="shereIcon">
          <div class="icon">
            <div class="iconSize iconSize4">
              <img src="../../../assets/img/task/task_weibo.png" alt="weibo">
            </div>
          </div>
          <div class="text">微博</div>
        </div>
      </div>
      <div class="bottom posC flex-row" @click="closePage">好的</div>
    </div>
  </div>
</template>
<script>
export default {
  methods: {
    closePage () {
      this.$emit('closePage')
    }
  }
}
</script>

<style lang="scss" scoped>
#whatCode{
  position: absolute;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 20;
  background: rgba($color: #000000, $alpha: .6);
  .whatCodeBox{
    width: 600px;
    color: #666;
    height: 718px;
    font-size: 28px;
    background: #FFFFFF;
    box-shadow: 0 2px 8px 0 rgba(135,134,134,0.50);
    border-radius: 16px;
    overflow: hidden;
    top: 50%;
    transform: translate(-50%, -50%);
    .w_top{
      background-image: linear-gradient(-250deg, #FFD500 0%, #FFAA00 100%);
      height: 120px;
      font-family: HelveticaNeue-Bold;
      font-size: 44px;
      color: #FFFFFF;
    }
    .w_tip{
      width: 450px;
      margin: 0 auto;
      text-align: center;
      line-height: 1.5;
      .w_t_text:nth-child(1){
        padding: 30px 0 10px;
        font-size: 32px;
        color: #232323;
      }
      .w_t_text:nth-child(2){
        padding-bottom: 38px;
        font-size: 26px;
        line-height: 36px;
        color: #232323;
      }
      .third {
        width: 482px;
      }
    }
    .shareBox{
      justify-content: space-between;
      padding: 50px;
      .shereIcon{
        .icon{
          height: 92px;
          display: flex;
          flex-flow: row nowrap;
          align-items: center;
          .iconSize{
            width: 61px;
            height: 92px;
          }
          .iconSize2{
            width: 64px;
            height: 63px;
          }
          .iconSize3{
            width: 75px;
            height: 60px;
          }
          .iconSize4{
            margin-top: -10px;
            width: 80px;
            height: 77px;
          }
        }
        .text {
          font-size: 22px;
          color: #232323
        }
      }
    }
    .bottom{
      bottom: 0;
      height: 90px;
      width: 100%;
      border-top: 1px solid #dbdbdb;
      font-family: PingFangSC-Regular;
      font-size: 30px;
      color: #3CA5E9;
    }
  }
}
</style>



