            <div class="count flex-row">
              <!-- <div class="count_inner flex-row nowrap">阅读提成奖励({{rewards.friend_reading_bonus}}) = 好友每日阅读奖励({{rewards.friend_reading_bonus / 2}}) * 2</div> -->
