            <div class="skillTip skillTip2">邀请你的家人、朋友、同事和同学成功率最高。</div>
            <div class="skillTip skillTip3">可以告诉邀请的朋友：注册可领<span class="redColor">{{commonState.inviteVcoin * 1}}</span>个掘金宝，一起冲百万富豪榜。</div>
