<script>
import headerNav from '@/components/task/header-nav'
import inviteLoop from '@/components/task/invite-friend/invite-loop'
import generateCode from '@/components/task/invite-friend/generateCode'
import whatCode from '@/components/task/invite-friend/whatCode'
import inviteList from '@/components/task/invite-friend/inviteList'
import ShareCommon from '@/components/share/shareCommon'
import IndexShare from '@/components/share/IndexShare'
import {mapState, mapMutations} from 'vuex'

export default {
  components: {
    headerNav,  // 头部
    inviteLoop, // 头部奖励循环
    generateCode, // 生成邀请码弹框
    whatCode,   // 什么是邀请码弹框
    inviteList, // 邀请码列表
    ShareCommon, // 公共分享引导
    IndexShare
  },
  computed: mapState({
    commonState: state => state.common
  }),
  data () {
    return {
      activeIndex: 0, // 0显示奖励规则 1显示我的邀请
      showCode: false, // 显示什么是邀请码的输入框
      showAll: false, // true显示查看所有邀请列表的人
      showShare: false, // 显示分享框
      rewards: {},     // 奖励规则
      inviteDataList: [], // 邀请人列表
      appShare: {
        count: 0,
        path: `/share_page/${Math.ceil(3 * Math.random())}`
      }, // app分享参数
      shareParams: {} // 分享参数
    }
  },
  methods: {
    ...mapMutations([
      'common_login_box',
      'setShowShare',
      'set_shareSuccessObj',
      'set_shareType',
      'common_set_vcoin'
    ]),
    // 头部分享
    headerShare () {
      this.appShare.count++
    },
    // 从子组件发动数据到父组件
    getArc (e) {
      if (e) {
        this.shareParams = e
        console.log(this.shareParams, 'this.shareParams')
      }
    },
    // 本页面app分享成功回调
    setCallBack ({type, way, success}) {
      this.shareSuccess({type, way, success})
    },
    // 微信组件
    shareSuccess (e) {
      this.$api.get('CALLBACK_REWARD', {tag: 'inviting_friend'}, res => {
        if (res.data.coin > 0) {
          let data = res.data
          data.type = this.commonState.shareArc['inviting_friend']
          this.set_shareType(7)
          this.set_shareSuccessObj({
            type: 7,
            shareSuccessObj: data
          })
          this.setShowShare(true)
        }
      })
    },
    // 显示app分享
    showAppShare () {
      this.appShare.count++
    },
    /**
     * 设置分享方式
     * @param {Number} type 传递分享类型  1 朋友圈，2 微信， 3 qq， 4, 二维码邀请， 5 链接邀请
     */
    setShareWay (type) {
      // app 分享
      if (this.$config.source_style >= 6) {
        if (type <= 3) {
          let id = 'weixin'
          let msg = {
            title: this.shareParams.title,
            content: this.shareParams.desc,
            thumbs: [this.shareParams.img_url],
            href: this.shareParams.shareLink
          }
          if (type === 1) {
            id = 'weixin'
            msg.type = 'web'
            msg.extra = {
              scene: 'WXSceneTimeline'
            }
          } else if (type === 2) {
            id = 'weixin'
            msg.type = 'web'
            msg.extra = {
              scene: 'WXSceneSession'
            }
          } else if (type === 3) {
            id = 'qq'
            // msg.type = 'text'
          }
          this.commonState.shares[id].send(msg, res => {
            if (type === 3) {
              this.$toast({
                message: '分享成功',
                duration: 1500
              })
            }
            this.setCallBack({type: 'app', way: type})
          }, err => {
            if (err.code === -2) {
              this.$toast({
                message: '已取消分享'
              })
              this.setCallBack({
                type: 'app',
                way: '3',
                success: 2
              })
            } else {
              this.$toast({
                message: '分享失败',
                duration: 1500
              })
            }
          })
        } else if (type === 4) {
          this.$router.push('/saveimage')
        } else if (type === 5) {
          this.$router.push('/short_link')
        }
      //  微信分享
      } else if (this.$utils.isWx()) {
        if (type <= 3) {
          this.showShare = true
        } else if (type <= 4) {
          this.$router.push('/saveimage')
        } else {
          this.$router.push('/short_link')
        }
      // 普通浏览器
      } else {
        if (type <= 4) {
          this.$router.push('/saveimage')
        } else {
          this.$router.push('/short_link')
        }
      }
    },
    // 关闭分享弹框
    closeShare (e) {
      if (e) {
        // this.$api()
      }
      this.showShare = false
    },
    // 跳转url
    toUrl (url) {
      this.$router.push(url)
    },
    // 计算假数据
    countList () {
      let arr = []
      for (let i = 0; i < 20; i++) {
        let tempData = {
          nickname: '掘金宝7182',
          avatar: 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png',
          create_time: '2018-10-08 20:03:50'
        }
        tempData.nickname = tempData.nickname + i
        arr.push(tempData)
      }
      return arr
    },
    // 获取邀请列表
    getList () {
      this.$api.get('INVITE_LIST', {page: 1}, res => {
        this.inviteDataList = res.data.data.slice(0, 5)
        // this.inviteDataList = this.countList()
        // res.data.total = 100
        if (res.data.total >= 5) {
          this.showAll = true
        }
      })
    },
    getRewardRules () {
      this.$api.get('MESSAGE_HINT', {}, res => {
        this.common_set_vcoin(res.data.reward_coefficient)
      })
    },
    // 切换奖励规则
    setIndex (index) {
      this.activeIndex = index
    },
    // 关闭什么是邀请码弹框
    closePage () {
      this.showCode = false
    },
    // 设置什么是邀请码的输入框
    setShowCode () {
      this.showCode = true
    },
    // 返回
    back () {
      this.$router.go(-1)
    }
  },
  mounted () {
    this.getList()
    this.getRewardRules()
    window.scrollTo(0, 0)
    // this.shareSuccess()
  }
}
</script>
