<template>
  <div id="generateCode">
    <div class="g-top flex-row">你的邀请码</div>
    <div class="g-code flex-row" :class="commonState.userInfo.invitation ? '' : 'f80'">{{commonState.userInfo.invitation ? commonState.userInfo.invitation : '******'}}</div>
    <div class="g-button flex-row jusC">
      <div class="button clipbtn" v-if="commonState.login_bol" @click="copyCode" :data-clipboard-text="copyText">点击复制</div>
      <div class="button" v-else @click="generateCode">生成邀请码</div>
    </div>
  </div>
</template>
<script>
import {mapState, mapMutations} from 'vuex'
import Clipboard from 'clipboard'
export default {
  data () {
    return {
      copyText: ''
    }
  },
  computed: mapState({
    commonState: state => state.common
  }),
  methods: {
    ...mapMutations([
      'common_login_box'
    ]),
    // 生成邀请码
    generateCode () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
    },
    // 复制邀请码
    copyCode () {
      if (Clipboard.isSupported()) {
        let clb = new Clipboard('.clipbtn')
        clb.on('success',  (e) => {
          this.$toast({
            message: '邀请码已复制,快发送给好友吧',
            duration: 1500
          })
          clb.destroy()
        })
        clb.on('error',  (e) => {
          this.$toast({
            message: '复制链接失败了',
            duration: 1500
          })
          clb.destroy()
        })
      }
    }
  },
  created () {
    this.copyText = `${this.commonState.userInfo.invitation}`
  },
  watch: {
    // 登录窗关闭
    'commonState.login_box': function (flag) {
      if ((!flag) && this.commonState.login_bol) {
        this.copyText = `${this.commonState.userInfo.invitation}`
      }
    }
  }
}
</script>
<style lang="scss" scoped>
#generateCode{
  width: 603px;
  height: 360px;
  top: -90px;
  position: absolute;
  box-sizing: border-box;
  box-shadow: 4px 4px 14px 0 rgba(135,134,134,0.50);
  border-radius: 10px;
  overflow: hidden;
  font-size: 34px;
  .g-top{
    height: 90px;
    color: #fff;
    width: 101%;
    background-image: linear-gradient(-180deg, #f8a641 0%, #F76B1C 100%);
  }
  .g-code{
    height: 103px;
    color: #232323;
    font-size: 50px;
    font-weight: 600;
  }
  .g-button{
    width: 100%;
    .button{
      width: 280px;
      height: 80px;
      text-align: center;
      line-height: 80px;
      background-image: linear-gradient(-180deg, #f8a641 0%, #F76B1C 100%);
      border-radius: 40px;
      color: #fff;
    }
  }
  .f80{
    font-size: 80px;
    color: #232323;
  }
}
</style>



