<template>
  <swiper id="inviteLoop" class="swiper-container1" :options='swiperOptions'>
    <swiper-slide class="swiper-slide flex-row" v-for="(item, index) in list" v-if="item.nickname" :key="index">
      <div class="leftIcon iconSize flex-row">
        <img src="../../../assets/img/my/message_active.png" alt="消息通告">
      </div>
      <div class="item"><span class="redColor">{{item.nickname}}</span>&nbsp;{{item.minutes_text}}{{item.tag_text}},<span v-html='item.remark'></span></div>
    </swiper-slide>
  </swiper>
</template>
<script>
import {swiper, swiperSlide} from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide
  },
  data () {
    return {
      list: [],
      swiperOptions: {
        direction: 'vertical',
        autoplay: {
          delay: 3000
        },
        loop: true
      }
    }
  },
  methods: {
    // 获取模拟数据
    getLoop () {
      this.$api.get('REWARD_LIST', {}, res => {
        this.list = res.data
      })
    },
    initData () {
      this.getLoop()
    }
  },
  created () {
    this.initData()
  }
}
</script>
<style lang="scss" scoped>
#inviteLoop{
  height: 60px;
  overflow: hidden;
  white-space: nowrap;
  position: fixed;
  font-size: 28px;
  line-height: 60px;
  color: #B79338;
  left: 0;
  right: 0;
  z-index: 2;
  background: #FFF1D5;
  .swiper-slide{
    .iconSize{
      margin-right: 10px;
      width: 30px;
      height: 29px;
    }
  }
}
</style>



